header {
  width: 100%;
}
header nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  padding: 24px 0px;
  background: #282828;
}
header nav .left {
  display: flex;
  justify-content: left;
  align-items: center;
}
@media (max-width: 767px) {
  header nav .left {
    display: none;
  }
}
header nav .left i {
  font-size: 25px;
  margin-left: 20px;
  color: white;
}
header nav .left i:hover {
  cursor: pointer;
  transition: 1s;
  transform: rotate(360deg);
  color: #c80000;
}
header nav .left span {
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  color: white;
  margin-left: 30px;
}
@media (max-width: 991px) {
  header nav .left span {
    font-size: 15px;
  }
}
@media (max-width: 767px) {
  header nav .left span {
    display: none;
  }
}
header nav .img {
  text-align: center;
}
@media (max-width: 767px) {
  header nav .img {
    text-align: left;
  }
}
header nav .right {
  display: flex;
  justify-content: right;
  align-items: center;
}
@media (max-width: 767px) {
  header nav .right {
    align-items: center;
    justify-content: right;
  }
}
header nav .right i {
  font-size: 25px;
  color: white;
  margin: 0px 20px;
}
header nav .right i:hover {
  cursor: pointer;
  transition: 1s;
  transform: rotate(360deg);
  color: #c80000;
}
@media (max-width: 991px) {
  header .carousel-inner h5 {
    display: none;
  }
}
@media (max-width: 991px) {
  header .carousel-inner p {
    display: none;
  }
}
header .headertitle {
  width: 100%;
}
header .headertitle .carousel-inner h5 {
  font-family: 'Jura', sans-serif;
  font-size: 41px;
  font-weight: 400;
  line-height: 48px;
  color: white;
}
header .headertitle .carousel-inner p {
  font-size: 19px;
  font-weight: 400;
  line-height: 23px;
  margin-top: 20px;
}
header .fotoheader {
  width: 100%;
  padding: 60px 0px;
  border-bottom: 2px dashed #E5E5E5;
}
@media (max-width: 767px) {
  header .fotoheader {
    display: none;
  }
}
header .fotoheader .car {
  text-align: center;
}
header .fotoheader .car p {
  font-size: 17px;
  font-weight: 500;
  line-height: 21px;
  color: white;
  margin-top: 30px;
}
header .fotoheader .car p:hover {
  cursor: pointer;
  transition: 1s;
  color: #ff0000;
}
main {
  width: 100%;
  padding-top: 30px;
}
main .img {
  position: relative;
}
main .img img {
  width: 100%;
}
main .text {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 70px;
  left: 25px;
}
main .text p {
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  color: white;
}
@media (max-width: 767px) {
  main .text p {
    font-size: 12px;
  }
}
main .text i {
  color: white;
  margin-left: 40px;
}
@media (max-width: 767px) {
  main .text i {
    display: none;
  }
}
main .box {
  position: relative;
  background: #272727;
  border-radius: 14px;
  width: 265px;
  height: 400px;
}
@media (max-width: 991px) {
  main .box {
    width: 350px;
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  main .box {
    width: 100%;
  }
}
main .box img {
  width: 100%;
  margin-top: 30%;
}
main .left {
  margin-top: 12px;
  margin-bottom: 25px;
}
main .left p {
  font-size: 14px;
  font-weight: 500;
  line-height: 28px;
  color: white;
}
main .left span {
  font-size: 14px;
  font-weight: 600;
  line-height: 28px;
  color: #ffffff;
}
main .left span a {
  font-size: 14px;
  font-weight: 600;
  line-height: 28px;
  color: #ff3232;
  margin-left: 20px;
}
main .plus {
  position: absolute;
  top: 20px;
  right: 30px;
}
@media (max-width: 991px) {
  main .plus {
    top: 70px;
  }
}
main .plus i {
  color: white;
}
main .plus i:hover {
  cursor: pointer;
  transition: 0.5s;
  padding: 10px;
  border-radius: 50%;
  background-color: #c80000;
}
main .box1 {
  position: relative;
}
@media (max-width: 991px) {
  main .box1 {
    margin-top: 50px;
  }
}
main .box1 img {
  width: 100%;
  height: 400px;
}
main .left1 {
