  top: 30px;
  right: 30px;
}
@media (max-width: 991px) {
  main .left1 {
    top: 80px;
  }
}
main .left1 i {
  color: white;
  font-size: 25px;
  margin-right: 18px;
}
main .left1 p {
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  color: white;
}
@media (max-width: 767px) {
  main .left1 p {
    font-size: 12px;
  }
}
