body {
  font-family: 'Montserrat', sans-serif;
  background: #373737;
}
body .scrollToTop-btn {
  z-index: 999;
  position: fixed;
  background: white;
  color: #111;
  width: 45px;
  height: 45px;
  right: 100px;
  bottom: 30px;
  font-size: 22px;
  text-align: center;
  line-height: 45px;
  border-radius: 3px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.3s ease;
}
body .scrollToTop-btn.active {
  right: 20px;
  opacity: 1;
}
body .reveal {
  position: relative;
  transform: translateY(50px);
  opacity: 0;
  transition: all 1.3s ease;
}
body .reveal.active {
  transform: translateY(0);
  opacity: 1;
}
